/*  */
.listItem {
  background: white;
  border-radius: 12px;
  padding: 16px;
  margin: 0 16px 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  transition: all 0.2s ease;
}

.listItem:hover {
  background: rgba(55, 53, 47, 0.04);
}

.listItem:active {
  transform: scale(0.98);
}

/*  -  */
.highlight {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.highlight:hover {
  background: linear-gradient(135deg, #5a6fd6 0%, #6a4291 100%);
}

.highlight .title {
  color: white;
}

.highlight .description {
  color: rgba(255, 255, 255, 0.8);
}

.highlight .icon {
  color: white;
}

.highlight .arrow {
  color: rgba(255, 255, 255, 0.8);
}

/*  */
.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.disabled:hover {
  background: white;
}

.disabled:active {
  transform: none;
}

/*  */
.leftContent {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
  min-width: 0;
}

/*  */
.icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 8px;
  background: rgba(55, 53, 47, 0.06);
  color: rgb(55, 53, 47);
  flex-shrink: 0;
}

.highlight .icon {
  background: rgba(255, 255, 255, 0.2);
}

/*  */
.textContent {
  display: flex;
  flex-direction: column;
  gap: 2px;
  min-width: 0;
}

.title {
  font-size: 15px;
  font-weight: 500;
  color: rgb(55, 53, 47);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.description {
  font-size: 12px;
  color: rgba(55, 53, 47, 0.5);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/*  */
.rightContent {
  display: flex;
  align-items: center;
  gap: 8px;
  flex-shrink: 0;
}

.arrow {
  color: rgba(55, 53, 47, 0.3);
}
